import Bmob from "hydrogen-js-sdk";
import customs from '@/utils/customs.js'
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"

let that;
let goodsId;
let uid = uni.getStorageSync("uid");
export default {
	components: {
		faIcon,
		loading
	},
	data() {
		return {
			custom_list:[],
			
			categoryList: [],
			subCategoryList: [],
			height: 0,
			categoryActive: 0,
			scrollTop: 0,
			scrollHeight: 0,
		}
	},
	methods: {
		
		scroll(e) {
			this.scrollHeight = e.detail.scrollHeight;
		},
		categoryClickMain(id,index) {
			this.categoryActive = index;
			this.scrollTop = -this.scrollHeight * index;
			
			this.get_detail(id)
		},
		
		get_detail(custom_id){
			const query = Bmob.Query("Bills");
			query.equalTo("type", '==', -1);
			query.equalTo("goodsId", '==',goodsId);
			
			query.equalTo("custom", '==', custom_id);
			query.include("operater","goodsId","opreater");
			query.find().then(res => {
				console.log(res)
				this.subCategoryList = res
			});
		},
		
	},
	onLoad(options) {
		goodsId = options.id
		
		that = this;
		customs.get_customList().then(res=>{
			that.custom_list = res
			
			that.get_detail(res[0].objectId)
		})
		
		this.height = uni.getSystemInfoSync().windowHeight;
	},

}
